.dropdown-mega-menu-md .btn.card-box:hover {
  transform: scale(1.1);
  z-index: 9;
  box-shadow: 0 5rem 14rem 0 hsla(0, 0%, 100%, .3), 0 0.8rem 2.3rem rgba(0, 0, 0, .6), 0 0.2rem 0.3rem rgba(0, 0, 0, .45);
}

.popover-custom-xl.popover-notification>.popover {
  height: 90%;
}

.popover-global-search > .popover {
  max-height: 90%;
  overflow-y: auto;
  overflow-x: hidden;
}

.global-search {
  padding: 20px 20px 0 20px;
  min-width: 414px;
}

.global-search ul {
  padding: 0 0 0 10px;
  border-left: dotted 1px #333;
  margin-left: 10px;
}

.global-search ul li {
  margin-left: 5px;
  padding: 3px 0;
}

.global-search > ul {
  margin-left: 0;
}

.global-search button {
  padding: 0;
  width: 100%;
  text-align: left;
}
